import { Component, OnInit, ViewChild } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {NgForm} from '@angular/forms';
import {MatDatepickerModule} from '@angular/material';
import { forEach } from '@angular/router/src/utils/collection';
import {ClientService} from "../../service/client.service";

@Component({
  selector: 'app-contact-us',
  templateUrl: './contact-us.component.html',
  styleUrls: ['./contact-us.component.css']
})
export class ContactUsComponent implements OnInit {

  constructor(private _http: HttpClient,private clientService : ClientService) { }
 
  clientModel = {
    time:'',
  };
  clientInfo:any[] | any;
  allButtons:any;
  times:string[] = ['10:00 AM','10:30 PM','11:00 AM',
    '11:30 AM','12:00 PM','12:30 PM','1:00 PM','1:30 PM',
    '2:00 PM','2:30 PM','3:00 PM','3:30 PM',
    '4:00 PM','4:30 PM','5:00 PM','5:30 PM',
    '6:00 PM','6:30 PM','7:00 PM','7:30 PM',
    '8:00 PM','8:30 PM','9:00 PM','9:30 PM',
    ]



  disableEnterKey(event) {
    if (event.keyCode === 13) {
      event.preventDefault();
      //console.log('nice try');
      return false;
      }
  }

  bookAppointment(elem){
    debugger;
    elem.target.style.backgroundColor='yellow';
    this.clientModel.time = elem.target.value;
    this.allButtons = document.querySelectorAll('input.btn');
    this.allButtons.forEach(function(elemVal){
      if(elemVal.style.backgroundColor === ""){
        elemVal.classList.add('disabled');
      }else{
        return;
      }
      
    });
    console.log(this.allButtons);
  }

  ngOnInit() {
    this.clientService.fetchClients().subscribe((data)=>{
      this.clientInfo = data;
    })
  }
}


