import { Component, OnInit } from '@angular/core';
import {Router} from "@angular/router";
import {HttpClient,  HttpHeaders} from '@angular/common/http';
import {Http, RequestOptions} from '@angular/http';
import {ClientService} from "../../service/client.service";

@Component({
  selector: 'app-email-contacts',
  templateUrl: './email-contacts.component.html',
  styleUrls: ['./email-contacts.component.css']
})
export class EmailContactsComponent implements OnInit {

  opts:any;
  emailId:string;
  data:any;
  contactInfo = {
    email:'',
    name:'',
    bookingDate:'',
    job:''
  }
  hairStore:any = [
    {'hairjob': 'Hair Cut', 'price':25},
    {'hairjob': 'Line Up', 'price':15},
    {'hairjob': 'Beard Clean Up', 'price':15}
  ]

  constructor(private _http: HttpClient, private router: Router,private clientService : ClientService) { }

  ngOnInit() {
  }

  emailUser(){
    let httpsHeaders = new HttpHeaders();
    httpsHeaders.set('Content-Type', 'application/x-www-form-urlencoded');
    this.data = {
      'email':this.contactInfo.email,
      'date': JSON.stringify(this.contactInfo.bookingDate),
      'name': this.contactInfo.name,
      'job': this.contactInfo.job
    };
    
    this.clientService.saveClient(this.data,{observe:'response'}).subscribe((data)=>{
      console.log('saving data:'+ data);
    });

    this.clientService.sendClientData(this.data,{headers:httpsHeaders,observe:'response'}).subscribe((data)=>{
      console.log(this.data);
      if(data){
        console.log("successfully sent email");
        this.router.navigate(['success']);
      }else{
        console.log("opps something went wrong");
      }
    })
  }

  disableEnterKey(event) {
    if (event.keyCode === 13) {
      event.preventDefault();
      //console.log('nice try');
      return false;
      }
  }

}
